<template>
    <div id="graph11" style="width:100%;height:1000px;"></div>
</template>

<script>
import 'echarts' 

export default {
  name:"Graph11",
  data(){
      return{}
  },
 methods:{
      draw(graph11){
// prettier-ignore
const hours = [
    '1月', '2月', '3月', '4月', '5月', '6月', '7月',
    '8月', '9月', '10月', '11月', '12月',
];
// prettier-ignore
const days = [
    '工业发电量', '预估总发电量', '全社会用电量'];
// prettier-ignore
const data = [[0, 0, 7276], [0, 1, 5897], [0, 2, 6578], [0, 3, 6231], [0, 4, 6479], [0, 5, 6861], [0, 6, 7586], [0, 7, 7384], [0, 8, 6750], [0, 9, 6392], [0, 10, 6540], [0, 11, 7234],
[1, 0, 7659], [1, 1, 6207], [1, 2, 6924], [1, 3, 6559], [1, 4, 6820], [1, 5, 7220], [1, 6, 7985], [1, 7, 7773], [1, 8, 7105], [1, 9, 6728], [1, 10, 6884], [1, 11, 7615], 
[2, 0, 7324], [2, 1, 5264], [2, 2, 6631], [2, 3, 6361], [2, 4, 6724], [2, 5, 7033], [2, 6, 7758], [2, 7, 7607], [2, 8, 6947], [2, 9, 6603], [2, 10, 6718], [2, 11, 8156], ]
    .map(function (item) {
    return [item[1], item[0], item[2] || '-'];
});
var option = {
    title: {
            text: '月度用电分布情况（2021年）',
            x:'center',
            y:'2%',
            textStyle: {
                        fontSize:30,
                        color:'#ffd900',
                    },
            textAlign:'left'
          },
  tooltip: {
    position: 'top'
  },
  grid: {
    height: '50%',
    top: '10%'
  },
  xAxis: {
    type: 'category',
    data: hours,
    splitArea: {
      show: true
    }
  },
  yAxis: {
    type: 'category',
    data: days,
    splitArea: {
      show: true
    }
  },
  visualMap: {
    min: 5000,
    max: 8000,
    calculable: true,
    orient: 'vertigo',
    left: '92%',
    bottom: '55%'
  },
  series: [
    {
      name: '电量数据',
      type: 'heatmap',
      data: data,
      label: {
        show: true
      },
      emphasis: {
        itemStyle: {
          shadowBlur: 10,
          shadowColor: 'rgba(0, 0, 0, 0.5)'
        }
      }
    }
  ]
};
    setTimeout(() =>{
        option && graph11.setOption(option);
    },10)
      } 
},
mounted(){
        var graph11 = this.$echarts.init(document.getElementById('graph11'));
        this.draw(graph11);
  },
}

</script>

